<template>
	<div class="box">
		<div class="header">
			<div>
				<div>
					<img v-lazy="require('@/assets/income_logo.png')">
					<span>{{ canWithdraw }}</span>
				</div>
				<span>{{ $t('message.currentCanDraw') }}/ETH</span>
			</div>
			<router-link to="/record?type=收益记录">
				<img v-lazy="require('@/assets/income_record.png')">
				<span>{{ $t('message.incomeRecord') }}</span>
			</router-link>
		</div>
		<self-grid :grid="grid"></self-grid>
		<div class="btn">
			<Button @click="draw" :disabled="canWithdraw <= 0" color="linear-gradient(#814ABC, #4C44D8)">{{ $t('message.draw') }}</Button>
			<span>{{ $t('message.gasPrice') }}：{{ gasPrice }} SFC</span>
		</div>
	</div>
</template>

<script>
	import { Button, Toast } from 'vant'
	import SelfGrid from '@/components/self-grid.vue'
	
	export default {
		components: { Button, SelfGrid },
		data() {
			return {
				canWithdraw: '0.00',
				grid: [
					{ img: require('@/assets/dividendIncome.png'), title: this.$t('message.dividendIncome'), amount: '0.00ETH' },
					{ img: require('@/assets/promoteIncome.png'), title: this.$t('message.promoteIncome'), amount: '0.00ETH' },
					{ img: require('@/assets/outAmount.png'), title: this.$t('message.outQuota'), amount: '0.00ETH' },
					{ img: require('@/assets/income_total.png'), title: this.$t('message.totalIncome'), amount: '0.00ETH' }
				],
				gasPrice: 0,
				sfcBalance: 0
			}
		},
		methods: {
			init() {
				if (this.$cache.income) {
					this.canWithdraw = this.$cache.income.canWithdraw
					this.grid[0].amount = this.$cache.income.dividendIncome
					this.grid[1].amount = this.$cache.income.promoteIncome
					this.grid[2].amount = this.$cache.income.outAmount
					this.grid[3].amount = this.$cache.income.totalIncome
					this.gasPrice = this.$cache.income.gasPrice
					this.sfcBalance = this.$cache.income.sfcBalance
				} else {
					this.$cache.income = {}
				}
			
				this.getData()
			},
			getData() {
				this.$contract.round.methods.GetCurrentGameStatus()
				.call({ from: this.$address }, (err, res) => {
					if (!err) {
						let outAmount = this.$fromWei(res.currentEther) * Number(res.profixMultiplier) - this.$fromWei(res.withdrawableTotal)
		
						this.$contract.round.methods.CurrentCanWithdrawProfix(this.$address).call((err, res) => {
							if (!err) {
								let st = this.$fromWei(res.st),
									dy = this.$fromWei(res.dy),
									canWithdraw = st + dy
		
								this.$contract.cost.methods.WithdrawCost(this.$toWei(`${canWithdraw}`)).call((err, res) => {
									if (!err) {
										this.gasPrice = this.$fromWei(res)
										this.$cache.income.gasPrice = this.gasPrice
									}
								})
		
								if (canWithdraw > outAmount) {
									this.canWithdraw = outAmount.toFixed(2)
								} else {
									this.canWithdraw = canWithdraw.toFixed(2)
								}
								this.grid[0].amount = `${st.toFixed(2)}ETH`
								this.grid[1].amount = `${dy.toFixed(2)}ETH`
		
								this.$cache.income.canWithdraw = this.canWithdraw
								this.$cache.income.dividendIncome = this.grid[0].amount
								this.$cache.income.promoteIncome = this.grid[1].amount
							}
						})
		
						this.grid[2].amount = `${outAmount.toFixed(2)}ETH`
						this.$cache.income.outAmount = this.grid[2].amount
					}
				})
			
				this.$contract.round.methods.TotalInOutAmount()
				.call({ from: this.$address }, (err, res) => {
					if (!err) {
						this.grid[3].amount = `${this.$fromWei(res.outEther, 2)}ETH`
						this.$cache.income.totalIncome = this.grid[3].amount
					}
				})
			
				this.$contract.erc20token.methods.balanceOf(this.$address).call((err, res) => {
					if (!err) {
						this.sfcBalance = this.$fromWei(res)
						this.$cache.income.sfcBalance = this.sfcBalance
					}
				})
			},
			draw() {
				if (this.sfcBalance < this.gasPrice) {
					return Toast.fail(this.$t('message.notSFC'))
				}
			
				this.$contract.round.methods.WithdrawProfix().send({
						from: this.$address
					}).on('transactionHash', txHash => {
						console.log(txHash)
					}).on('error', console.error)
					.on('receipt', res => {
						this.getData()
					})
			}
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang="scss" scoped>
	.btn {
		display: flex;
		align-items: center;
		flex-direction: column;
		justify-content: center;
		&>button {
			width: 360px;
			height: 80px;
			font-size: 32px;
			border-radius: 50px;
		}
		&>span {
			color: #A1BDE1;
			font-size: 32px;
			margin-top: 20px;
		}
	}
	.header {
		display: flex;
		align-items: flex-start;
		justify-content: flex-end;
		padding-right: 20px;
		&>div {
			display: flex;
			align-items: center;
			flex-direction: column;
			margin-right: 46px;
			color: #fff;
			&>div {
				position: relative;
				width: 236px;
				height: 240px;
				&>img {
					width: 100%;
					height: 100%;
				}
				&>span {
					display: inline-block;
					width: 200px;
					height: 200px;
					line-height: 200px;
					position: absolute;
					top: 30px;
					left: 25px;
					font-size: 48px;
					font-weight: bold;
				}
			}
			&>span {
				font-size: 28px;
				margin-top: 40px;
				font-weight: 400;
			}
		}
	
		&>a {
			display: flex;
			align-items: center;
			color: #505688;
			font-size: 32px;
			font-weight: 400;
			&>img {
				width: 42px;
				height: 55px;
				margin-right: 20px;
			}
		}
	}
	.box {
		padding-top: 40px;
	}
</style>
