<template>
	<div class="box">
		<div class="header">
			<span :class="show ? 'active' : ''" @click="show = true">{{ $t('message.joinRecord') }}</span>
			<span :class="show ? '' : 'active'" @click="show = false">{{ $t('message.incomeRecord') }}</span>
		</div>
		<record
		  v-show="show"
		  :title="[$t('message.joinAmount'), $t('message.joinTime')]"
		  :list="joinList"
		  span="12"
		></record>
		<record
		  v-show="!show"
		  :title="[$t('message.type'), $t('message.number'), $t('message.time')]"
		  :list="incomeList"
		  span="8"
		></record>
	</div>
</template>

<script>
	import Record from '@/components/home-record.vue'
	
	export default {
		components: { Record },
		data() {
			return {
				show: true,
				joinList: [],
				incomeList: []
			}
		},
		methods: {
			init() {
				if (this.$cache.record) {
					this.joinList = this.$cache.record.joinList
					this.incomeList = this.$cache.record.incomeList
				} else {
					this.$cache.record = {}
				}
				
				this.getData()
			},
			getData() {
				this.$http.getJoin(this.$address).then(res => {
					this.joinList = []
					res.forEach(item => {
						let timestamp = this.$web3.eth.abi.decodeParameter('uint256', item.topics[2]),
							amount = this.$web3.eth.abi.decodeParameter('uint256', item.topics[3]),
							date = new Date(Number(timestamp) * 1000),
							year = date.getFullYear(),
							month = date.getMonth() + 1,
							day = date.getDate()
							
						this.joinList.unshift({
							amount: `${this.$fromWei(amount)}ETH`,
							time: `${year}/${month}/${day}`
						})
					})
					
					this.$cache.record.joinList = this.joinList
				})
				
				this.$http.getIncome(this.$address).then(res => {
					this.incomeList = []
					res.forEach(item => {
						let amount = this.$web3.eth.abi.decodeParameter('uint256', item.topics[2]),
							type = this.$web3.eth.abi.decodeParameter('uint8', item.topics[3]),
							timestamp = this.$web3.eth.abi.decodeParameter('uint256', item.data),
							date = new Date(Number(timestamp) * 1000),
							year = date.getFullYear(),
							month = date.getMonth() + 1,
							day = date.getDate()
							
						if (Number(type) >= 0 && Number(type) < 15) {
							type = this.$t('message.sharingAward')
						} else if (Number(type) > 20 && Number(type) < 25) {
							type = `V${type.slice(1)}`
						} else if (Number(type) > 30 && Number(type) < 35) {
							type = this.$t('message.tieAward')
						} else if (Number(type) === 40) {
							type = this.$t('message.dividend')
						}
							
						this.incomeList.unshift({
							type: type,
							amount: `+${this.$fromWei(amount)} ETH`,
							time: `${year}/${month}/${day}`
						})
					})
					
					this.$cache.record.incomeList = this.incomeList
				})
			}
		},
		mounted() {
			if (this.$route.query.type === '收益记录') {
				this.show = false
			}
			this.init()
		}
	}
</script>

<style lang="scss" scoped>
	.header {
		width: 500px;
		height: 80px;
		color: #E8E9EA;
		font-size: 32px;
		font-weight: bold;
		margin: 0 auto 20px;
		border-radius: 50px;
		background-color: #1F1E48;
		&>span {
			display: inline-block;
			width: 50%;
			height: 100%;
			line-height: 80px;
			text-align: center;
		}
		&>.active {
			border-radius: 50px;
			background: linear-gradient(#814ABC, #4C44D8);
		}
	}
	.box {
		width: 100vw;
		height: 100vh;
		padding: 20px;
		overflow: hidden;
	}
</style>
