<template>
	<div class="box">
		<pre v-if="$i18n.locale === 'zh-CN'">
	<span>联盟节点说明：</span>
	1.联盟合伙人限定13名，需要支付200ETH购买；
	2.凡参与联盟节点的玩家，身份将会提升至V2；
	3.联盟节点每周进行一次分红奖励。分红时间以7日后出块速度决定。
		  <p>当前已有{{ player }}名玩家参与联盟节点</p>
		  <p>您当前需支付<span>{{ prices }}ETH</span>可进行参与</p>
		</pre>
		<pre v-else>
	<span>Alliance partner:</span>
	①.50 eth for top 20 participants and 100 eth for later 20 participants;
	②.All players participating in the alliance partners will be promoted to V2;
	③.The alliance partners will receive bonus once a week. Dividend time is determined by the rate of block issuance in 7 days.
		  <p>Currently, there are {{ player }} players participating in the alliance partners</p>
		  <p>You need to pay <span>{{ prices }}ETH</span> to participate</p>
		</pre>
		<div class="content">
			<div class="title">
				<i></i>
				<span>{{ $t('message.joinNode') }}</span>
			</div>
			<div class="input">
				<input type="number" v-model="amount">
				<span>ETH</span>
			</div>
			<div class="amount">
				<img v-lazy="require('@/assets/eth.png')">
				<span>{{ balance }} ETH</span>
			</div>
		</div>
		<Button disabled @click="join" color="linear-gradient(#814ABC, #4C44D8)">{{ $t('message.join') }}</Button>
	</div>
</template>

<script>
	import { Button, Toast } from 'vant'
	
	export default {
		components: { Button },
		data() {
			return {
				player: '0',
				prices: '0',
				balance: '0.00',
				amount: 0
			}
		},
		methods: {
			init() {
				if (this.$cache.node) {
					this.balance = this.$cache.node.balance
					this.player = this.$cache.node.player
					this.prices = this.$cache.node.prices
					this.amount = this.prices
				} else {
					this.$cache.node = {}
				}
				
				this.getData()
			},
			getData() {
				this.$web3.eth.getBalance(this.$address).then(balance => {
					this.balance = this.$fromWei(balance, 2)
					this.$cache.node.balance = this.balance
				})
			
				this.$contract.levelSub.methods._paymentedCount().call((err, res) => {
					if (!err) {
						this.player = res
						this.$cache.node.player = res
					}
				})
			
				this.$contract.levelSub.methods._paymentLv2Prices().call((err, res) => {
					if (!err) {
						this.prices = this.$fromWei(res)
						this.amount = this.prices
						this.$cache.node.prices = this.prices
					}
				})
			},
			join() {
				if (this.amount < this.prices) {
					return Toast.fail(`${this.$t('message.minJoin')}${this.prices}ETH`)
				}
			
				this.$contract.levelSub.methods.PaymentToUpgradeNoderL2().send({
					from: this.$address,
					value: this.$toWei(`${this.amount}`)
				}).on('transactionHash', txHash => {
					console.log(txHash)
				}).on('error', console.error)
				.on('receipt', res => {
					this.amount = 0
					this.getData()
				})
			}
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang="scss" scoped>
	.content {
		padding: 0 27px;
		margin-top: 30px;
		.amount {
			display: flex;
			align-items: center;
			font-size: 24px;
			font-weight: 400;
			color: #FCFCFC;
			margin-top: 20px;
			&>img {
				width: 40px;
				height: 40px;
				margin-right: 12px;
			}
		}
		.input {
			width: 100%;
			padding: 20px 0 16px;
			color: #fff;
			font-size: 48px;
			font-weight: bold;
			display: flex;
			align-items: center;
			border-bottom: 2px solid #6F7AB2;
			&>input {
				font-size: 48px;
				font-weight: bold;
				width: 100%;
				border: none;
				outline: none;
				background-color: transparent;
			}
		}
		&>.title {
			display: flex;
			align-items: center;
			&>i {
				width: 8px;
				height: 32px;
				background-color: #4D95FC;
				margin-right: 12px;
			}
			&>span {
				font-size: 32px;
				color: #fff;
				font-weight: bold;
			}
		}
	}
	.box {
		padding: 40px 34px 0;
		&>pre {
			width: 100%;
			color: #A1BDE1;
			font-size: 24px;
			font-weight: 400;
			text-align: left;
			line-height: 40px;
			white-space: pre-wrap;
			white-space: -moz-pre-wrap;
			white-space: -pre-wrap;
			white-space: -o-pre-wrap;
			*word-wrap: break-word;
			*white-space : normal ;
			background-color: #282350;
			padding: 20px 44px 0px 26px;
			&>span {
				font-size: 28px;
			}
			&>p {
				font-weight: bold;
				text-align: center;
				&:last-child {
					line-height: 1px;
					margin-top: -20px;
					font-size: 26px;
					&>span {
						font-size: 38px;
					}
				}
			}
		}
		&>button {
			width: 340px;
			height: 80px;
			font-size: 32px;
			font-weight: 400;
			margin-top: 125px;
			border-radius: 50px;
		}
	}
</style>
