<template>
	<div class="van-tabbar-page">
		<div class="content van-content">
			<span>{{ $t('message.inviteCode') }}</span>
			<div class="grade">
				<!-- <router-link to="/grade">
					<img v-lazy="require(`@/assets/v${grade}.png`)">
				</router-link> -->
				<div>
					<img v-lazy="require(`@/assets/v${grade}.png`)">
				</div>
				<span>{{ invitationCode }}</span>
				<img
				  v-if="isBind"
				  v-lazy="require('@/assets/copy.png')"
				  v-clipboard:copy="invitationCode"
				  v-clipboard:success="onCopySuccess"
				  v-clipboard:error="onCopyError"
				>
				<img v-else @click="goBind" v-lazy="require('@/assets/copy.png')">
			</div>
			<div class="list" v-if="isBind">
				<div>
					<div>
						<img v-lazy="require('@/assets/link.png')">
						<span>{{ $t('message.inviteLink') }}</span>
					</div>
					<img
					  v-lazy="require('@/assets/copy.png')"
					  v-clipboard:copy="invitationLink"
					  v-clipboard:success="onCopySuccess"
					  v-clipboard:error="onCopyError"
					>
				</div>
				<router-link to="/invitationList">
					<div>
						<img v-lazy="require('@/assets/list.png')">
						<span>{{ $t('message.inviteList') }}</span>
					</div>
					<img v-lazy="require('@/assets/next.png')">
				</router-link>
				<div class="eye">
					<div>
						<img v-lazy="require('@/assets/myInvite.png')">
						<span>{{ $t('message.myInvite') }}</span>
					</div>
					<span v-if="showInviter">{{ myInviter }}</span>
					<span v-else>******</span>
					<img v-if="showInviter" @click="showInviter = !showInviter" v-lazy="require('@/assets/eye_show.png')">
					<img v-else @click="showInviter = !showInviter" v-lazy="require('@/assets/eye.png')">
				</div>
				<router-link to="/compensate">
					<div>
						<img v-lazy="require('@/assets/drawSFC.png')">
						<span>{{ $t('message.drawSFC') }}</span>
					</div>
					<img v-lazy="require('@/assets/next.png')">
				</router-link>
				<router-link :to="path">
					<div>
						<img v-lazy="require('@/assets/node.png')">
						<span>{{ $t('message.allianceNode') }}</span>
					</div>
					<img v-lazy="require('@/assets/next.png')">
				</router-link>
				<router-link to="/manager">
					<div>
						<img v-lazy="require('@/assets/manager.png')">
						<span>{{ $t('message.manager') }}</span>
					</div>
					<img v-lazy="require('@/assets/next.png')">
				</router-link>
				<router-link class="exit" to="/exit">
					<div>
						<img v-lazy="require('@/assets/exit.png')">
						<span>{{ $t('message.exit') }}</span>
					</div>
					<img v-lazy="require('@/assets/next.png')">
				</router-link>
			</div>
			<div class="list" v-else>
				<div>
					<div>
						<img v-lazy="require('@/assets/link.png')">
						<span>{{ $t('message.inviteLink') }}</span>
					</div>
					<img @click="goBind" v-lazy="require('@/assets/copy.png')">
				</div>
				<div @click="goBind">
					<div>
						<img v-lazy="require('@/assets/list.png')">
						<span>{{ $t('message.inviteList') }}</span>
					</div>
					<img v-lazy="require('@/assets/next.png')">
				</div>
				<div class="eye">
					<div>
						<img v-lazy="require('@/assets/myInvite.png')">
						<span>{{ $t('message.myInvite') }}</span>
					</div>
					<span>*******</span>
					<img @click="goBind" v-lazy="require('@/assets/eye.png')">
				</div>
				<div @click="goBind">
					<div>
						<img v-lazy="require('@/assets/drawSFC.png')">
						<span>{{ $t('message.drawSFC') }}</span>
					</div>
					<img v-lazy="require('@/assets/next.png')">
				</div>
				<div @click="goBind">
					<div>
						<img v-lazy="require('@/assets/node.png')">
						<span>{{ $t('message.allianceNode') }}</span>
					</div>
					<img v-lazy="require('@/assets/next.png')">
				</div>
				<div @click="goBind">
					<div>
						<img v-lazy="require('@/assets/manager.png')">
						<span>{{ $t('message.manager') }}</span>
					</div>
					<img v-lazy="require('@/assets/next.png')">
				</div>
				<div class="exit" @click="goBind">
					<div>
						<img v-lazy="require('@/assets/exit.png')">
						<span>{{ $t('message.exit') }}</span>
					</div>
					<img v-lazy="require('@/assets/next.png')">
				</div>
			</div>
		</div>
		<self-tabbar active="my"></self-tabbar>
	</div>
</template>

<script>
	import selfTabbar from '@/components/self-tabbar.vue'
	import { Dialog, Toast } from 'vant'
	import { isWallet  } from '@/static/initData'
	
	export default {
		name: "my",
		components: { selfTabbar },
		data() {
			return {
				grade: 0,
				invitationCode: "******",
				invitationLink: "",
				myInviter: "******",
				showInviter: false,
				path: '/node',
				isBind: localStorage.getItem('isBind') === 'true'
			}
		},
		methods: {
			init() {
			    if (this.$cache.my) {
			        this.path = this.$cache.my.path
			        this.grade = this.$cache.my.grade
			        if (this.$cache.my.invitationCode) {
			            this.invitationCode = this.$cache.my.invitationCode
			            this.invitationLink = this.$cache.my.invitationLink
			            this.myInviter = this.$cache.my.myInviter
			        }
			    } else {
			        this.$cache.my = {}
			    }
			
			    this.getData()
			},
			getData() {
			    this.$contract.levelSub.methods.LevelOf(this.$address).call((err, res) => {
			        if (!err) {
			            this.grade = Number(res)
			            this.$cache.my.grade = this.grade
			
			            this.$contract.levelSub.methods.CanUpgradeLv(this.$address).call((err, res) => {
			                if (!err && Number(res) > 0 && this.grade < 4) {
			                    this.show = true
			                    this.updateGrade = res
			                }
			            })
			        }
			    })
			
			    this.$contract.recommend.methods.GetIntroducer(this.$address).call((err, res) => {
			        if (!err && Number(res) > 0) {
			            this.$contract.recommend.methods.AddressToShortCode(res).call((error, result) => {
			                if (!error) {
			                    if (Number(result) > 0) {
			                        this.myInviter = this.$web3.utils.hexToString(result)
			                    } else {
			                        this.myInviter = res
			                    }
			
			                    this.$cache.my.myInviter = this.myInviter
			                }
			            })
			        }
			    })
			
			    this.$contract.recommend.methods.AddressToShortCode(this.$address).call((err, res) => {
			        if (!err) {
			            let invitationCode = this.$web3.utils.hexToString(res)
			            if (invitationCode === "") { return }
			            
			            this.invitationCode = invitationCode
			            this.invitationLink = `${location.href}?invitationCode=${invitationCode}`
			
			            this.$cache.my.invitationCode = invitationCode
			            this.$cache.my.invitationLink = this.invitationLink
			        }
			    })
			
			    this.$contract.levelSub.methods.Noders().call((err, res) => {
			        if (!err) {
			            let flag = false
			            res.forEach(item => {
			                if (item.toUpperCase() === this.$address.toUpperCase()) {
			                    flag = true
			                }
			            })
			
			            this.path = flag ? "/nodeIncome" : "/node"
			            this.$cache.my.path = this.path
			        }
			    })
			},
			goBind() {
				Dialog.alert({
					message: '请先绑定邀请码!'
				}).then(() => {
					this.$router.push({ path: '/?bind=1' })
				})
				return false
			},
			onCopySuccess() {
			    Toast.success(this.$t('message.copySuccess'))
			},
			onCopyError() {
			    Toast.fail(this.$t('message.copyFail'))
			}
		},
		mounted() {
			if (isWallet) {
			    this.init()
			} else {
			    this.isBind = false
			}
		}
	}
</script>

<style lang="scss" scoped>
	.content {
		text-align: left;
		padding: 22px 40px 20px;
		border-radius: 20px 20px 0 0;
		&>.list {
			width: 100%;
			&>div:first-child {
				&>div>img {
					height: 46px;
				}
				&>img {
					width: 36px;
					height: 36px;
				}
			}
			&>.eye {
				padding-right: 14px;
				&>img {
					width: 50px;
					height: 35px;
				}
			}
			&>.exit>div>img {
				width: 42px;
				height: 53px;
				margin-right: 32px;
			}
			&>div, &>a {
				width: 100%;
				display: flex;
				align-items: center;
				justify-content: space-between;
				padding: 34px 28px 34px 16px;
				border-bottom: 1px solid #0D0B3A;
				&>div {
					display: flex;
					align-items: center;
					&>img {
						width: 48px;
						height: 48px;
						margin-right: 26px;
					}
					&>span {
						color: #A1BDE1;
						font-size: 30px;
						font-weight: 400;
					}
				}
				&>span {
					color: #A1BDE1;
					font-size: 32px;
					font-weight: 400;
				}
				&>img {
					width: 23px;
					height: 41px;
				}
			}
		}
		&>.grade {
			width: 100%;
			padding: 38px 0;
			margin: 18px 0;
			position: relative;
			border-radius: 8px;
			display: flex;
			align-items: center;
			justify-content: center;
			background: linear-gradient(to right, #9237D8, #5C38D9);
			&>div {
				position: absolute;
				left: 40px;
				width: 120px;
				height: 76px;
				&>img {
					width: 100%;
					height: 100%;
				}
			}
			&>span {
				color: #fff;
				font-size: 52px;
				font-weight: 300;
			}
			&>img:last-child {
				position: absolute;
				right: 40px;
				width: 36px;
				height: 36px;
			}
		}
		&>span {
			color: #fff;
			font-size: 40px;
			font-weight: 400;
		}
	}
	.van-content {
		position: absolute;
		left: 0;
		bottom: 50px;
		width: 100vw;
		background-color: #312E56;
	}
</style>
