import axios from 'axios'
import { $web3, contract } from './static/initData'

const http = {
	getNode: (address) => {
		return new Promise((resolve, reject) => {
			send(
				contract.noderAssets._address,
				sha3('Log_BonusDetail(address,uint256,uint256)'),
				true,
				$web3.eth.abi.encodeParameter('address', address)
			).then(resolve).catch(reject)
		})
	},
	getManager: (address) => {
		return new Promise((resolve, reject) => {
			send(
				contract.managerAssets._address,
				sha3('Log_BonusDetail(address,uint256,uint256)'),
				true,
				$web3.eth.abi.encodeParameter('address', address)
			).then(resolve).catch(reject)
		})
	},
	getDrawRedress: (address) => {
		return new Promise((resolve, reject) => {
			send(
				contract.redress._address,
				sha3('Event_WithdrawRedress(address,uint256,uint256)'),
				true,
				$web3.eth.abi.encodeParameter('address', address)
			).then(resolve).catch(reject)
		})
	},
	getRedress: () => {
		return new Promise((resolve, reject) => {
			send(
				contract.redress._address,
				sha3('Event_AddNewRedress(address,uint256,uint256)'),
				true
			).then(resolve).catch(reject)
		})
	},
	getIncome: (address) => {
		return new Promise((resolve, reject) => {
			send(
				contract.round._address,
				sha3('Log_ProfixHistory(address,uint256,uint8,uint256)'),
				true,
				$web3.eth.abi.encodeParameter('address', address)
			).then(resolve).catch(reject)
		})
	},
	getJoin: (address) => {
		return new Promise((resolve, reject) => {
			send(
				contract.round._address,
				sha3('Log_NewDeposited(address,uint256,uint256)'),
				true,
				$web3.eth.abi.encodeParameter('address', address)
			).then(resolve).catch(reject)
		})
	},
	getChange: (address) => {
		return new Promise((resolve, reject) => {
			send(
				contract.tokenChanger._address,
				sha3('Log_ChangedToken(address,uint256,uint256,uint256)'),
				true,
				$web3.eth.abi.encodeParameter('address', address)
			).then(resolve).catch(reject)
		})
	},
	getNewJoin: () => {
		return new Promise((resolve, reject) => {
			send(
				contract.round._address,
				sha3('Log_NewDeposited(address,uint256,uint256)')
			).then(resolve).catch(reject)
		})
	},
	getLuckPlayer: () => {
		return new Promise((resolve, reject) => {
			send(
				contract.luckAssetsPoolB._address,
				sha3('Log_Winner(address,uint256,uint256)'),
				true
			).then(resolve).catch(reject)
		})
	}
}

function sha3(str) {
	return $web3.utils.sha3(str)
}

function send(contract, method, genesis, address) {
	return new Promise((resolve, reject) => {
		if (address != undefined) {
			axios.get('https://api-cn.etherscan.com/api',{
				params: {
					"module": 'logs',
					"action": 'getLogs',
					"fromBlock": 0,
					"toBlock": 'latest',
					"address": contract,
					"topic0": method,
					"topic1": address,
					"apikey":'APJQ2KZQX7GQMD9FTUK986WSBRVEB7TUYU'
				}
			}).then(res => {
				resolve(res.data.result)
			}).catch(reject)
		} else {
			$web3.eth.getBlockNumber().then(block => {
				axios.get('https://api-cn.etherscan.com/api',{
					params: {
						"module": 'logs',
						"action": 'getLogs',
						"fromBlock": genesis ? 0 : block - 10800,
						"toBlock": 'latest',
						"address": contract,
						"topic0": method,
						"apikey":'APJQ2KZQX7GQMD9FTUK986WSBRVEB7TUYU'
					}
				}).then(res => {
					resolve(res.data.result)
				}).catch(reject)
			})
		}
	})
}

export default http