<template>
	<div class="box">
		<self-list
		  :title="$t('message.inviteList')"
		  :subtitle="[$t('message.myBuddy'), $t('message.grade'), $t('message.totalJoin'), $t('message.community')]"
		  :list="list"
		  span="6"
		></self-list>
	</div>
</template>

<script>
	import SelfList from '@/components/self-list.vue'
	
	export default {
		components: { SelfList },
		data() {
			return {
				list: []
			}
		},
		methods: {
			init() {
				if (this.$cache.myLnvitationList) {
					this.list = this.$cache.myLnvitationList.list
				} else {
					this.$cache.myLnvitationList = {}
				}
		
				this.getData()
			},
			async getData() {
				let list = [],
					arr = (await this.$contract.recommend.methods.RecommendList(this.$address, 0).call())
				
				if (arr.length > 0) {
					arr.forEach(async item => {
						let partner = await this.$contract.recommend.methods.AddressToShortCode(item).call(),
							state = await this.$contract.levelSub.methods.LevelOf(item).call(),
							totalJoin = this.$fromWei((await this.$contract.round.methods.GetCurrentGameStatus().call({ from: item })).currentEther),
							member = await this.$contract.recommend.methods.TeamMemberTotal(item).call()
							
						if (Number(partner) === 0) {
							partner = this.$t('message.notCode')
						} else {
							partner = this.$web3.utils.hexToString(partner)
						}
						
						let row = {
							partner: partner,
							state: require(`@/assets/v${state}.png`),
							totalJoin: totalJoin,
							member: member
						}
							
						list.push({ ...row })
					})
				}
				
				this.list = list
				this.$cache.myLnvitationList.list = list
			}
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang="scss" scoped>
	.box {
		width: 100vw;
		height: 100vh;
		overflow: hidden;
		padding: 40px 20px;
	}
</style>
