import Vue from 'vue'
import Web3 from 'web3'
import ERC20Token from "./ERC20Token.json"
import TokenChanger from "./TokenChanger.json"
import Recommend from "./Recommend.json"
import Round from "./Round.json"
import LevelSub from "./LevelSub.json"
import Statistics from "./Statistics.json"
import Cost from "./Cost.json"
import Redress from "./Redress.json"
import LuckAssetsPoolA from "./LuckAssetsPoolA.json"
import LuckAssetsPoolB from "./LuckAssetsPoolB.json"
import Turing from "./Turing.json"
import ManagerAssets from "./ManagerAssets.json"
import NoderAssets from "./NoderAssets.json"

let $web3, contract, isWallet = true
if (window.ethereum) {
    $web3 = new Web3(ethereum)
} else if (window.web3) {
    $web3 = new Web3(web3.currentProvider)
} else {
    isWallet = false
}

const ChainId = 1
if (isWallet) {
    contract = {
        erc20token: new $web3.eth.Contract(ERC20Token.abi, ERC20Token.networks[ChainId].address),
        tokenChanger: new $web3.eth.Contract(TokenChanger.abi, TokenChanger.networks[ChainId].address),
        recommend: new $web3.eth.Contract(Recommend.abi, Recommend.networks[ChainId].address),
        round: new $web3.eth.Contract(Round.abi, Round.networks[ChainId].address),
        levelSub: new $web3.eth.Contract(LevelSub.abi, LevelSub.networks[ChainId].address),
        statistics: new $web3.eth.Contract(Statistics.abi, Statistics.networks[ChainId].address),
        cost: new $web3.eth.Contract(Cost.abi, Cost.networks[ChainId].address),
        redress: new $web3.eth.Contract(Redress.abi, Redress.networks[ChainId].address),
        luckAssetsPoolA: new $web3.eth.Contract(LuckAssetsPoolA.abi, LuckAssetsPoolA.networks[ChainId].address),
        luckAssetsPoolB: new $web3.eth.Contract(LuckAssetsPoolB.abi, LuckAssetsPoolB.networks[ChainId].address),
        turing: new $web3.eth.Contract(Turing.abi, Turing.networks[ChainId].address),
        managerAssets: new $web3.eth.Contract(ManagerAssets.abi, ManagerAssets.networks[ChainId].address),
        noderAssets: new $web3.eth.Contract(NoderAssets.abi, NoderAssets.networks[ChainId].address)
    }
}

const fromWei = (balance, digit) => {
    if (digit) {
        return Number($web3.utils.fromWei(balance, 'ether')).toFixed(digit)
    }
    return Number($web3.utils.fromWei(balance, 'ether'))
}

const toWei = (balance) => {
    return $web3.utils.toWei(balance, 'ether')
}

export { $web3, contract, fromWei, toWei, isWallet }