<template>
	<div class="box">
		<img v-lazy="require('@/assets/exit_logo.png')">
		<pre>
	{{ $t('message.exitTitle1') }}
	{{ $t('message.exitTitle2') }}
		</pre>
		<div class="content">
			<div>
				<div>
					<img v-lazy="require('@/assets/outAmount.png')">
					<span>{{ $t('message.totalJoin') }}</span>
				</div>
				<span>{{ totalJoin }} ETH</span>
			</div>
			<div>
				<div>
					<img v-lazy="require('@/assets/dividendIncome.png')">
					<span>{{ $t('message.totalIncome') }}</span>
				</div>
				<span>{{ totalIncome }} ETH</span>
			</div>
			<div>
				<div>
					<img v-lazy="require('@/assets/exit.png')">
					<span>{{ $t('message.canExit') }}</span>
				</div>
				<span>{{ exitAmount }} ETH</span>
			</div>
		</div>
		<Button :disabled="disabled" @click="exit" color="linear-gradient(#814ABC, #4C44D8)">{{ $t('message.define') }}</Button>
	</div>
</template>

<script>
	import { Button } from 'vant'

	export default {
		components: { Button },
		data() {
			return {
				disabled: true,
				totalJoin: '0.00',
				exitAmount: '0.00',
				totalIncome: '0.00'
			}
		},
		methods: {
			init() {
			    if (this.$cache.exit) {
			        this.disabled = this.$cache.exit.disabled
			        this.totalJoin = this.$cache.exit.totalJoin
			        this.exitAmount = this.$cache.exit.exitAmount
			        this.totalIncome = this.$cache.exit.totalIncome
			    } else {
			        this.$cache.exit = {}
			    }
			
			    this.getData()
			},
			getData() {
			    this.$contract.round.methods.GetCurrentGameStatus()
			    .call({ from: this.$address }, (err, res) => {
			        if (!err) {
			            let lastTimestamp = new Date(Number(res.latestDepositInTime) * 1000 + 7200000).getTime()
			
			            this.$contract.round.methods.TotalInOutAmount()
			            .call({ from: this.$address }, (error, result) => {
			                if (!error) {
			                    let inEther = this.$fromWei(result.inEther),
			                        outEther = this.$fromWei(result.outEther)
			
			                    this.totalJoin = `${inEther.toFixed(2)}`
			                    this.totalIncome = `${outEther.toFixed(2)}`
			                    this.exitAmount = `${(inEther - outEther).toFixed(2)}`
			                    this.disabled = !(inEther > outEther && new Date().getTime() > lastTimestamp)
			
			                    this.$cache.exit.disabled = this.disabled
			                    this.$cache.exit.totalJoin = this.totalJoin
			                    this.$cache.exit.exitAmount = this.exitAmount
			                    this.$cache.exit.totalIncome = this.totalIncome
			                }
			            })
			        }
			    })
			},
			exit() {
			    this.$contract.round.methods.Redeem().send({
			        from: this.$address
			    }).on('error' , console.error)
			    .on('receipt' , result=>{
			        this.$router.back()
			    })
			}
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang="scss" scoped>
	.content {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-wrap: wrap;
		color: #A1BDE1;
		font-weight: 400;
		&>div {
			width: 320px;
			display: flex;
			align-items: center;
			flex-direction: column;
			padding: 50px 0 26px;
			font-size: 40px;
			border-radius: 15px;
			background-color: #25234D;
			&>div {
				display: flex;
				align-items: center;
				font-size: 32px;
				margin-bottom: 8px;
				&>img {
					width: 40px;
					height: 40px;
					margin-right: 26px;
				}
			}
			&:nth-of-type(2)>div>img {
				width: 44px;
				margin-right: 24px;
			}
			&:last-child {
				width: 100%;
				margin-top: 22px;
				flex-direction: row;
				padding: 38px 0 30px;
				justify-content: center;
				&>div {
					margin: 0;
					font-size: 28px;
					margin-right: 128px;
					flex-direction: column;
					&>img {
						width: 42px;
						height: 53px;
						margin: 0;
						margin-bottom: 12px;
					}
				}
			}
		}
	}
	.box {
		padding: 84px 38px 0;
		&>img {
			width: 230px;
			height: 240px;
		}
		&>pre {
			width: 100%;
			margin: 24px 0;
			color: #A1BDE1;
			font-size: 28px;
			font-weight: 400;
			text-align: left;
			line-height: 40px;
			border-radius: 15px;
			padding: 30px 24px 0;
			border: 2px solid #5A5DE6;
			white-space: pre-wrap;
			white-space: -moz-pre-wrap;
			white-space: -pre-wrap;
			white-space: -o-pre-wrap;
			*word-wrap: break-word;
			*white-space : normal ;
		}
		&>button {
			width: 340px;
			height: 80px;
			font-size: 32px;
			font-weight: 400;
			margin-top: 40px;
			border-radius: 50px;
		}
	}
</style>
