<template>
	<div class="box">
		<self-ball
		  :amount="current"
		  :title="`${$t('message.currentCanDraw')}/SFC`"
		></self-ball>
		<self-grid :grid="grid"></self-grid>
		<Button disabled color="linear-gradient(#814ABC, #4C44D8)">{{ $t('message.draw') }}</Button>
	</div>
</template>

<script>
	import SelfBall from '@/components/self-ball.vue'
	import SelfGrid from '@/components/self-grid.vue'
	import { Button } from 'vant'
	
	export default {
		components: { SelfBall, SelfGrid, Button },
		data() {
			return {
				grid: [
					{ img: require('@/assets/compensate_total.png'), title: this.$t('message.totalRemedy'), amount: '0 SFC' },
					{ img: require('@/assets/compensate_await.png'), class: 'await', title: this.$t('message.awaitFreed'), amount: '0 SFC' },
					{ img: require('@/assets/compensate_everyday.png'), class: 'everyday', title: this.$t('message.everydayFreed'), amount: '0 SFC' },
					{ img: require('@/assets/compensate_record.png'), class: 'record', title: this.$t('message.drawRecord'), text: this.$t('message.details'), path: '/compensateRecord' }
				],
				current: '0.00',
				disabled: false
			}
		},
		methods: {
		    init() {
		        if (this.$cache.compensate) {
		            this.current = this.$cache.compensate.current
		            this.grid[0].amount = this.$cache.compensate.total
		            this.grid[1].amount = this.$cache.compensate.wait
		            this.grid[2].amount = this.$cache.compensate.everyday
		        } else {
		            this.$cache.compensate = {}
		        }
		
		        this.getData()
		    },
		    getData() {
		        this.$contract.redress.methods._withdrawProp().call((err, result) => {
		            if (!err) {
		                let prop = Number(result / 100)
		
		                this.$contract.redress.methods.RedressInfo()
		                .call({ from: this.$address }, (err, res) => {
		                    if (!err) {
		                        this.current = `${this.$fromWei(res.cur, 2)}`
		                        this.grid[0].amount = `${this.$fromWei(res.total)} SFC`
		                        this.grid[1].amount = `${this.$fromWei(res.total) - this.$fromWei(res.withdrawed)} SFC`
		                        this.grid[2].amount = `${this.$fromWei(res.total) * prop} SFC`
		
		                        this.$cache.compensate.current = this.current
		                        this.$cache.compensate.total = this.grid[0].amount
		                        this.$cache.compensate.wait = this.grid[1].amount
		                        this.$cache.compensate.everyday = this.grid[2].amount
		
		                        this.disabled = !(this.current > 0)
		                    }
		                })
		            }
		        })
		    },
			withdraw() {
			    this.$contract.redress.methods.WithdrawRedress().send({
			        from: this.$address
			    }).on("transactionHash", txHash => {
			        console.log(txHash)
			    }).on("error", console.error)
			    .on("receipt", res => {
			        this.getData()
			    })
			}
		},
		mounted() {
		    this.init()
		}
	}
</script>

<style lang="scss" scoped>
	.box {
		padding-top: 60px;
		&>button {
			width: 340px;
			height: 80px;
			font-size: 32px;
			font-weight: 400;
			margin-top: 30px;
			border-radius: 50px;
		}
	}
</style>
