<template>
	<div class="box">
		<div class="record">
			<not-record v-if="!list.length"></not-record>
			<div class="header">{{ $t('message.changeRecord') }}</div>
			<div class="content">
				<div v-for="(item, index) of list" :key="index">
					<div>
						<span>兑换成功</span>
						<span>+{{ item.sfc }} SFC</span>
					</div>
					<div>
						<span>{{ item.time }}</span>
						<span>-{{ item.eth }} ETH</span>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import NotRecord from '@/components/not-record.vue'
	import { isWallet  } from '@/static/initData'
	
	export default {
		components: { NotRecord },
		data() {
			return {
				list: []
			}
		},
		methods: {
			init() {
				if (this.$cache.changeRecord) {
					this.list = this.$cache.changeRecord.list
				} else {
					this.$cache.changeRecord = {}
				}
				
				this.getData()
			},
			getData() {
				this.$http.getChange(this.$address).then(res => {
					this.list = []
					res.forEach(item => {
						let amount = this.$web3.eth.abi.decodeParameter('uint256', item.topics[2]),
							data = this.$web3.eth.abi.decodeParameters(['uint256', 'uint256'], item.data),
							date = new Date(Number(data[1]) * 1000),
							year = date.getFullYear(),
							month = date.getMonth() + 1,
							day = date.getDate()
							
						this.list.unshift({
							sfc: this.$fromWei(data[0], 2),
							eth: this.$fromWei(amount, 2),
							time: `${year}.${month}.${day}`
						})
					})
					
					this.$cache.changeRecord.list = this.list
				})
			}
		},
		mounted() {
			if (isWallet) {
			    this.init()
			}
		}
	}
</script>

<style lang="scss" scoped>
	.content {
		width: 100%;
		padding-top: 14px;
		height: calc(100% - 100px);
		overflow-y: auto;
		&>div {
			width: 100%;
			margin-bottom: 6px;
			padding: 0 50px;
			&>div {
				display: flex;
				align-items: center;
				justify-content: space-between;
				color: #A1BDE1;
				font-size: 20px;
				line-height: 30px;
				&:first-child>span:first-child {
					color: #28AC79;
				}
			}
		}
	}
	.header {
		width: 600px;
		height: 100px;
		color: #fff;
		font-size: 32px;
		font-weight: bold;
		margin: 0 auto;
		line-height: 100px;
		border-bottom: 2px solid #0D0B3A;
	}
	.record {
		position: relative;
		width: 100%;
		height: 100%;
		border-radius: 10px;
		background-color: #25234D;
	}
	.box {
		width: 100vw;
		height: 100vh;
		padding: 25px 20px 32px;
	}
</style>
