<template>
	<div class="box">
		<self-list
		  :title="$t('message.drawRecord')"
		  :subtitle="[$t('message.type'), $t('message.number'), $t('message.time')]"
		  :list="list"
		  span="8"
		></self-list>
	</div>
</template>

<script>
	import SelfList from '@/components/self-list.vue'
	
	export default {
		components: { SelfList },
		data() {
			return {
				list: []
			}
		},
		methods: {
			init() {
				if (this.$cache.compensateRecord) {
					this.list = this.$cache.compensateRecord.list
				} else {
					this.$cache.compensateRecord = {}
				}
				
				this.getData()
			},
			getData() {
				this.$http.getDrawRedress(this.$address).then(res => {
					this.list = []
					res.forEach(item => {
						let amount = this.$web3.eth.abi.decodeParameter('uint256', item.topics[2]),
							// total = this.$web3.eth.abi.decodeParameter('uint256', item.data),
							date = new Date(Number(item.timeStamp) * 1000),
							year = date.getFullYear(),
							month = date.getMonth() + 1,
							day = date.getDate()
							
						this.list.unshift({
							type: '补偿',
							amount: `${this.$fromWei(amount)} SFC`,
							time: `${year}.${month}.${day}`
						})
					})
					
					this.$cache.compensateRecord.list = this.list
				})
			}
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang="scss" scoped>
	.box {
		width: 100vw;
		height: 100vh;
		overflow: hidden;
		padding: 40px 20px;
	}
</style>
