<template>
	<self-manager
	  :amount="amount"
	  :time="time"
	  :disabled="disabled"
	  path="/nodeRecord"
	  @draw="withdraw"
	></self-manager>
</template>

<script>
	import { Button } from 'vant'
	import SelfManager from '@/components/self-manager'

	export default {
		components: { Button, SelfManager },
		data() {
			return {
				amount: '0.00',
				time: '2020/03/23',
				disabled: true
			}
		},
		methods: {
			init() {
				if (this.$cache.nodeIncome) {
					this.amount = this.$cache.nodeIncome.amount
					this.time = this.$cache.nodeIncome.time
					this.disabled = this.$cache.nodeIncome.disabled
				} else {
					this.$cache.nodeIncome = {}
				}
				
				this.getData()
			},
			getData() {
				this.$contract.noderAssets.methods._latestBonusTime().call((err, res) => {
					if (!err) {
						let date = new Date(Number(res) * 1000),
							year = date.getFullYear(),
							month = date.getMonth() + 1 < 10 ? `0${date.getMonth() + 1}` : date.getMonth() + 1,
							day = date.getDate() < 10 ? `0${date.getDate()}` : date.getDate()
				
						this.time = `${year}/${month}/${day}`
						this.$cache.nodeIncome.time = this.time
					}
				})
				
				this.$contract.noderAssets.methods.MyProfix()
				.call({ from: this.$address }, (err, res) => {
					if (!err) {
						this.amount = this.$fromWei(res, 2)
						this.$cache.nodeIncome.amount = this.amount
						
						this.disabled = this.amount <= 0
						this.$cache.nodeIncome.disabled = this.disabled
					}
				})
			},
			withdraw() {
				this.$contract.noderAssets.methods.WithdrawProfix().send({
					from: this.$address
				}).on('transactionHash', txHash => {
					console.log(txHash)
				}).on('error', console.error)
				.on('receipt', res => {
					this.disabled = true
					this.getData()
				})
			}
		},
		mounted() {
			this.init()
		}
	}
</script>