<template>
	<div class="box">
		<self-ball
		  :title="`${$t('message.currentJoin')}/ETH`"
		  :amount="currentJoin"
		></self-ball>
		<!-- <router-link to="/record" class="record">
			<img v-lazy="require('@/assets/income_record.png')">
			<span>{{ $t('message.joinRecord') }}</span>
		</router-link> -->
		<self-grid :grid="grid"></self-grid>
		<div class="btn">
			<router-link v-if="!disabled" to="/join">{{ $t('message.againJoin') }}</router-link>
			<Button v-if="disabled" disabled>{{ $t('message.againJoin') }}</Button>
			<span v-if="disabled">参与后48小时可以再次参与!</span>
		</div>
	</div>
</template>

<script>
	import SelfBall from '@/components/self-ball'
	import SelfGrid from '@/components/self-grid'
	import { Button } from 'vant'
	
	export default {
		components: { SelfBall, SelfGrid, Button },
		data() {
			return {
				currentJoin: '0.00',
				grid: [
					{ img: require('@/assets/again_out.png'), title: this.$t('message.outAmount'), amount: '0.00ETH' },
					{ img: require('@/assets/again_can.png'), title: this.$t('message.canJoinBalance'), amount: '0.00ETH' },
					{ img: require('@/assets/outAmount.png'), title: this.$t('message.totalJoin'), amount: '0.00ETH' },
					// { img: require('@/assets/again_share.png'), title: this.$t('message.share'), amount: '0.00ETH' }
					{ img: require('@/assets/compensate_record.png'), title: this.$t('message.joinRecord'), text: this.$t('message.details'), path: '/record' }
				],
				disabled: true
			}
		},
		methods: {
			init() {
			    if (this.$cache.againJoin) {
			        this.currentJoin = this.$cache.againJoin.currentJoin
			        this.grid[0].amount = this.$cache.againJoin.outAmount
			        this.grid[1].amount = this.$cache.againJoin.canJoin
			        this.grid[2].amount = this.$cache.againJoin.totalJoin
			        // this.grid[3].amount = this.$cache.againJoin.expectedIncome
			        this.disabled = this.$cache.againJoin.disabled
			    } else {
			        this.$cache.againJoin = {}
			    }
			
			    this.getData()
			},
			getData() {
			    this.$contract.round.methods.GetCurrentGameStatus()
			    .call({ from: this.$address }, (err, res) => {
			        if (!err) {
			            let currentJoin = this.$fromWei(res.currentEther),
			                canJoin = this.$fromWei(res.depositMaxLimit) - currentJoin,
			                outAmount = currentJoin * Number(res.profixMultiplier) - this.$fromWei(res.withdrawableTotal),
			                latestJoin = new Date(Number(res.latestDepositInTime) * 1000 + (48 * 3600000)).getTime()
			
			            this.currentJoin = `${currentJoin.toFixed(2)}`
			            this.grid[0].amount = `${outAmount.toFixed(2)}ETH`
			            this.grid[1].amount = `${canJoin.toFixed(2)}ETH`
			
			            this.$cache.againJoin.currentJoin = this.currentJoin
			            this.$cache.againJoin.outAmount = this.grid[0].amount
			            this.$cache.againJoin.canJoin = this.grid[1].amount
						
						this.disabled = (canJoin <= 0 || new Date().getTime() < latestJoin)
			            this.$cache.againJoin.disabled = this.disabled
			        }
			    })
			
			    this.$contract.round.methods.TotalInOutAmount()
			    .call({ from: this.$address }, (error, result) => {
			        if (!error) {
			            let inEther = this.$fromWei(result.inEther)
			
			            this.grid[2].amount = `${inEther.toFixed(2)}ETH`
			            this.$cache.againJoin.totalJoin = this.grid[2].amount
			        }
			    })
				
				// this.$contract.recommend.methods.DirectValidMembersCount(this.$address)
				// .call((err, res) => {
				// 	if (!err) {
				// 		console.log(res)
				// 	}
				// })
			}
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang="scss" scoped>
	.btn {
		display: flex;
		align-items: center;
		flex-direction: column;
		justify-content: center;
		&>button, &>a {
			border: none;
			width: 360px;
			height: 80px;
			font-size: 32px;
			border-radius: 50px;
		}
		&>a {
			color: #fff;
			line-height: 80px;
			background: linear-gradient(#814ABC, #4C44D8);
		}
		&>span {
			color: #A1BDE1;
			font-size: 32px;
			margin-top: 20px;
		}
	}
	.grid {
		margin-top: 28px;
	}
