<template>
	<div class="ball">
		<div id="chart"></div>
		<span>{{ title }}</span>
	</div>
</template>

<script>
	import echarts from 'echarts'
	import '@/components/echarts-liquidfill.min'
	
	export default {
		props: {
			amount: [Number, String],
			title: String
		},
		data() {
			return {
				ball: ''
			}
		},
		mounted() {
			this.ball = echarts.init(document.getElementById('chart'))
			this.ball.setOption({
			    series: [{
			        type: 'liquidFill',
			        radius: '100%',
			        data: [{
			            value: 0.5,
			            direction: 'left',
			            itemStyle: {
			                normal: {
			                    color: {
			                    	type: 'linear',
			                    	x: 0,
			                    	y: 0,
			                    	x2: 0,
			                    	y2: 1,
			                    	colorStops:[
			                    		{ offset: 0, color: '#814ABC' },
			                    		{ offset: 1, color: '#4C44D8' }
			                    	],
			                    	globalCoord: false
			                    }
			                }
			            }
			        }, {
			            value: 0.5,
			            direction: 'right',
			            itemStyle: {
			                normal: {
			                    color: {
									type: 'linear',
									x: 0,
									y: 0,
									x2: 0,
									y2: 1,
									colorStops:[
										{ offset: 0, color: '#814ABC' },
										{ offset: 1, color: '#4C44D8' }
									],
									globalCoord: false
								}
			                }
			            }
			        }],
			        backgroundStyle: {
			            borderWidth: 0.5,
			            borderColor: {
							type: 'linear',
							x: 0,
							y: 0,
							x2: 0,
							y2: 1,
							colorStops:[
								{ offset: 0, color: '#814ABC' },
								{ offset: 1, color: '#4C44D8' }
							],
							globalCoord: false
						},
			            color: '#fff'
			        },
			        label: {
			            normal: {
			                formatter: this.amount,
			                textStyle: {
			                    fontSize: 20
			                }
			            }
			        },
			        outline: {
			            show: false
			        }
			    }]
			})
		},
		watch: {
		    amount(val) {
		        this.ball.setOption({
		            series: [{
		                label: {
		                    normal: {
		                        formatter: val
		                    }
		                }
		            }]
		        })
		    }
		}
	}
</script>

<style lang="scss" scoped>
	.ball {
	    width: 240px;
		margin: 0 auto;
		display: flex;
		align-items: center;
		flex-direction: column;
	    &>#chart {
	        width: 100%;
	        height: 240px;
	        overflow: visible;
			margin-bottom: 20px;
			// border: 1px solid #814ABC;
			// border-radius: 50%;
	    }
	    &>span {
	        color: #fff;
	        font-size: 28px;
	    }
	}
</style>
