import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/home/Home.vue'
import Join from '../views/home/view/Join.vue'
import Income from '../views/home/view/Income.vue'
import AgainJoin from '../views/home/view/AgainJoin.vue'
import Record from '../views/home/view/Record.vue'
import Change from '../views/change/Change.vue'
import ChangeRecord from '../views/change/ChangeRecord.vue'
import My from '../views/my/My.vue'
import Grade from '../views/my/view/Grade.vue'
import InvitationList from '../views/my/view/InvitationList.vue'
import Compensate from '../views/my/view/Compensate.vue'
import CompensateRecord from '../views/my/view/CompensateRecord.vue'
import Node from '../views/my/view/Node.vue'
import NodeIncome from '../views/my/view/NodeIncome.vue'
import NodeRecord from '../views/my/view/NodeRecord.vue'
import Manager from '../views/my/view/Manager.vue'
import ManagerRecord from '../views/my/view/ManagerRecord.vue'
import Exit from '../views/my/view/Exit.vue'

Vue.use(VueRouter)

const routes = [
	{
		path: '/',
		name: 'home',
		component: Home
	},
	{
		path: '/join',
		name: 'join',
		component: Join
	},
	{
		path: '/income',
		name: 'income',
		component: Income
	},
	{
		path: '/againJoin',
		name: 'againJoin',
		component: AgainJoin
	},
	{
		path: '/record',
		name: 'record',
		component: Record
	},
	{
		path: '/change',
		name: 'change',
		component: Change
	},
	{
		path: '/changeRecord',
		name: 'changeRecord',
		component: ChangeRecord
	},
	{
		path: '/my',
		name: 'my',
		component: My
	},
	{
		path: '/grade',
		name: 'grade',
		component: Grade
	},
	{
		path: '/invitationList',
		name: 'invitationList',
		component: InvitationList
	},
	{
		path: '/compensate',
		name: 'compensate',
		component: Compensate
	},
	{
		path: '/node',
		name: 'node',
		component: Node
	},
	{
		path: '/manager',
		name: 'manager',
		component: Manager
	},
	{
		path: '/exit',
		name: 'exit',
		component: Exit
	},
	{
		path: '/compensateRecord',
		name: 'compensateRecord',
		component: CompensateRecord
	},
	{
		path: '/managerRecord',
		name: 'managerRecord',
		component: ManagerRecord
	},
	{
		path: '/nodeIncome',
		name: 'nodeIncome',
		component: NodeIncome
	},
	{
		path: '/nodeRecord',
		name: 'nodeRecord',
		component: NodeRecord
	}
]

const router = new VueRouter({
	mode: 'history',
	base: process.env.BASE_URL,
	routes
})

export default router
