<template>
	<div class="box">
		<self-list
		  :title="$t('message.dividendRecord')"
		  :list="list"
		  span="12"
		></self-list>
	</div>
</template>

<script>
	import SelfList from '@/components/self-list.vue'
	
	export default {
		components: { SelfList },
		data() {
			return {
				list: []
			}
		},
		methods: {
			init() {
				if (this.$cache.managerRecord) {
					this.list = this.$cache.managerRecord.list
				} else {
					this.$cache.managerRecord = {}
				}
				
				this.getData()
			},
			getData() {
				this.$http.getManager(this.$address).then(res => {
					this.list = []
					res.forEach(item => {
						let timeStamp = this.$web3.eth.abi.decodeParameter('uint256', item.topics[2]),
							amount = this.$web3.eth.abi.decodeParameter('uint256', item.topics[3]),
							date = new Date(Number(timeStamp) * 1000),
							year = date.getFullYear(),
							month = date.getMonth() + 1,
							day = date.getDate(),
							hour = date.getHours(),
							minute = date.getMinutes()
							
						this.list.unshift({
							amount: `${this.$fromWei(amount, 2)}ETH`,
							time: `${year}.${month}.${day} ${hour}:${minute}`
						})
					})
					
					this.$cache.managerRecord.list = this.list
				})
			}
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang="scss" scoped>
	.box {
		width: 100vw;
		height: 100vh;
		overflow: hidden;
		padding: 40px 20px;
	}
</style>
